.App {
  text-align: center;
  color:#fff;
}

.App-logo {
  height: 15vmin;
  position: absolute;
  left:1px;
  pointer-events: none;
  scale:0.5;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 5vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.App-h1{
  color: #fff;
  font-size: calc(30px + 2vmin);
}

.App-quote{
  font-size: calc(30px + 2vmin);
  justify-content: center;
  background-color: #1c0a59;
  border-radius: 20vmin;
  margin-left: calc(30px + 2vmin);
  margin-right: calc(30px + 2vmin);
  margin-top: calc(20vmin);
  padding-top: calc(20vmin);
  height : calc(40vmin);

}

.App-Button{
  font-size: calc(30px + 2vmin);
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);

  }
  to {
    transform: rotate(360deg);
  }
}
